Wee.view.addView({"search":"{{ #hits|each }}<li class=\"search-results__entry\"><a href=\"{{ url }}\" class=\"search-results__link\"><h4 class=\"search-results__title\">{{ title }}</h4>{{ #description|notEmpty }}<p class=\"search-results__desc\">{{ description }}</p>{{ /description }}</a></li>{{ /hits }}"});